<template>
  <v-dialog v-model="dialog.show" persistent max-width="780px">
    <v-card>
      <v-card-title>
        <div class="title">
          {{ $vuetify.lang.t(messages.title) }}
        </div>
      </v-card-title>
      <v-divider class="mx-4"></v-divider>
      <v-form @submit.prevent="save">
        <v-card-text>
          <v-container>
            <v-row wrap>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-text-field
                  v-model="form.code"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.expenses_nature.cols.nature_code.title'
                    )
                  "
                  prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  dense
                  filled
                  readonly
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="8" class="mb-0 py-0"> </v-col>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.name"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.medical_act.cols.name.title')
                  "
                  @blur="$v.form.name.$touch()"
                  @input="$v.form.name.$touch()"
                  :error-messages="nameErrors"
                  prepend-inner-icon="mdi-rename-box mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.price"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.medical_act.cols.price.title')
                  "
                  prepend-inner-icon="mdi-cash mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  dense
                  suffix="FCFA"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-autocomplete
                  v-model="form.medical_areas"
                  :items="itemsMedical_areas"
                  :loading="isLoading"
                  :search-input.sync="search"
                  color="#007bff"
                  item-text="name"
                  return-object
                  item-value="id"
                  :label="
                    $vuetify.lang.t('$vuetify.medical_areas.cols.name.title')
                  "
                  prepend-inner-icon="mdi-hospital mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  dense
                  clearable
                ></v-autocomplete>
              </v-col>
              <v-col cols="12" sm="2" class="mb-0 py-0">
                <v-btn
                  class="mx-2"
                  dark
                  dense
                  @click="createD()"
                  color="#007bff"
                  max-width="2px"
                >
                  <v-icon dark>
                    mdi-plus
                  </v-icon>
                </v-btn>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="mb-0 py-0"
                v-if="form.coefficient == null"
              >
                <v-text-field
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.medical_act.cols.coefficient.title'
                    )
                  "
                  prepend-inner-icon="mdi-aspect-ratio mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  readonly
                  :value="
                    form.medical_areas == null
                      ? ''
                      : form.medical_areas.coefficient
                  "
                  dense
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="mb-0 py-0"
                v-if="form.quote_external == null"
              >
                <v-text-field
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.medical_act.cols.quote_external.title'
                    )
                  "
                  prepend-inner-icon="mdi-percent mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  readonly
                  :value="
                    form.medical_areas == null
                      ? ''
                      : form.medical_areas.quote_external
                  "
                  dense
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="mb-0 py-0"
                v-if="form.quote_internal == null"
              >
                <v-text-field
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.medical_act.cols.quote_internal.title'
                    )
                  "
                  prepend-inner-icon="mdi-percent mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  readonly
                  :value="
                    form.medical_areas == null
                      ? ''
                      : form.medical_areas.quote_internal
                  "
                  dense
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="mb-0 py-0"
                v-if="form.coefficient != null"
              >
                <v-text-field
                  color="#007bff"
                  v-model="form.coefficient"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.medical_act.cols.coefficient.title'
                    )
                  "
                  prepend-inner-icon="mdi-aspect-ratio mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="mb-0 py-0"
                v-if="form.quote_external != null"
              >
                <v-text-field
                  color="#007bff"
                  v-model="form.quote_external"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.medical_act.cols.quote_external.title'
                    )
                  "
                  prepend-inner-icon="mdi-margin mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="mb-0 py-0"
                v-if="form.quote_internal != null"
              >
                <v-text-field
                  color="#007bff"
                  v-model="form.quote_internal"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.medical_act.cols.quote_internal.title'
                    )
                  "
                  prepend-inner-icon="mdi-margin mdi-dark mdi-18px"
                  class="font-weight-bold"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-divider class="mx-4" style="margin-top: -25px"></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn @click="dialog.show = false">
            {{ $vuetify.lang.t("$vuetify.btn.close") }}
          </v-btn>
          <v-btn color="#007bff" :loading="loading" dark type="submit">
            {{ $vuetify.lang.t(messages.submit) }}
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
    <form-areas :dialog="dialogForm"></form-areas>
  </v-dialog>
</template>

<script>
import FormAreas from "./FormDM";
import { existNameAct } from "../../helpers/Validator";

import FormMixin from "./../../mixins/Common/Form.vue";
// import {mapGetters} from "vuex";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          code: null,
          medical_areas: null,
          name: null,
          coefficient: null,
          price: null,
          quote_internal: null,
          quote_external: null
        };
      }
    }
  },
  data: () => ({
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    urlItems: "medical_act",
    urlMedical_areas: "medical_areas",
    isLoading: false,
    search: null,
    itemsMedical_areas: []
  }),
  watch: {
    search(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlMedical_areas,
          params: params
        })
        .then(response => {
          console.log("respp", this.form.medical_areas);
          self.itemsMedical_areas = response.data.content;
        })
        .finally(() => (this.isLoading = false));
    }
  },
  created() {},
  validations() {
    let validators = {
      form: {
        name: {
          required,
          unique: existNameAct,
          maxLength: maxLength(255)
        }
      }
    };
    return validators;
  },
  methods: {
    createD() {
      this.dialogForm.show = true;
      this.formData = {
        id: null,
        name: null,
        coefficient: null,
        number_account: null,
        quote_internal: null,
        quote_external: null
      };
    },
    // Fonction pour récuperer les types de roles dans la store

    // Fonction pour récuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        medical_areas: this.form.medical_areas,
        coefficient:
          this.form.coefficient === null
            ? this.form.medical_areas.coefficient
            : this.form.coefficient,
        price: this.form.price,
        quote_internal:
          this.form.quote_internal === null
            ? this.form.medical_areas.quote_internal
            : this.form.quote_internal,
        name: this.form.name,
        quote_external:
          this.form.quote_external === null
            ? this.form.medical_areas.quote_external
            : this.form.quote_external
      };
      return data;
    },
    setForm(item) {
      // Données envoyées lors de la modification
      this.form.id = item.id;
      this.form.code = item.code;
      this.form.name = item.name;
      this.form["oldName"] = item.name;
      this.form.price = item.price;
      this.form.coefficient = item.coefficient;
      this.form.quote_internal = item.quote_internal;
      this.form.quote_external = item.quote_external;
      this.form.medical_areas = item.medical_areas["id"];
      this.itemsMedical_areas.push(item.medical_areas);
      // this.form.coefficient =
      //   item.medical_areas === null
      //     ? item.coefficient
      //     : item.medical_areas["coefficient"];
      // console.log("coef", this.form.coefficient);
      // this.form.quote_internal =
      //   item.medical_areas === null
      //     ? item.quote_internal
      //     : item.medical_areas["quote_internal"];
      // this.form.quote_external =
      //   item.medical_areas === null
      //     ? item.quote_external
      //     : item.medical_areas["quote_external"];
      // this.form.medical_areas =
      //   item.medical_areas === null ? "" : item.medical_areas["id"];
      // this.itemsMedical_areas.push(item.medical_areas);
    }
  },
  computed: {
    // Gestion des erreurs du formulaire
    nameErrors() {
      let errors = [];
      if (!this.$v.form.name.$dirty) return errors;
      !this.$v.form.name.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.medical_act.cols.name.required")
        );
      !this.$v.form.name.unique &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.medical_act.cols.name.unique")
        );

      return errors;
    },
    priceErrors() {
      let errors = [];
      if (!this.$v.form.price.$dirty) return errors;
      !this.$v.form.price.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.medical_act.cols.price.required")
        );

      return errors;
    },
    quote_externalErrors() {
      let errors = [];
      if (!this.$v.form.quote_external.$dirty) return errors;

      !this.$v.form.quote_external.quote_external &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.medical_act.cols.quote_external.invalid"
          )
        );
      return errors;
    },
    phoneErrors() {
      let errors = [];
      if (!this.$v.form.phone.$dirty) return errors;
      !this.$v.form.phone.phoneR &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.medical_act.cols.phone.invalid")
        );
      return errors;
    }
  },
  components: {
    FormAreas
  }
};
</script>
